export default{
    //存
    set(k,val){
       return localStorage.setItem(k,val)
    },
    //取
    get(k){
        return localStorage.getItem(k)
    },
    //删除
    remove(k){
        return localStorage.removeItem(k)
    },
    //清空
    clear(k){
        return localStorage.clear(k)
    }
}