/*
    建立请求文件
*/

import axios from "axios";
import qs from "qs";
//引进ui框架弹窗
import {Message} from "element-ui"

//引进存储方法
import local from "@/units/local"
import router from "../router";

// 请求拦截器  发请求的时候，可以做一些事情，携带一些参数
axios.interceptors.request.use(config => {
    var tk = local.get("token");
    if (tk) {
        // 把token携带到请求拦截器中
        config.headers.Authorization = tk;
    }
    return config;
});

// 响应拦截器  请求拿到数据过后，统一处理一些结果
axios.interceptors.response.use((response) => {
    // console.log(response.data);
    if(response.data.code != undefined&&response.data.msg!=undefined){
        let {code,msg}=response.data;
        //判断请求结果 有msg和code 才弹出窗口，否则不做拦截
        if(code===0){
            Message({
                type:"success",
                message:msg
            })
        }else if(code===1){
            Message.error(msg)
        }
    }
    return response;
},(err)=>{
    // console.log(err);
    //当出现无效的token就会触发err函数
    if(err.response.data.code == 401){
        //先清空本地错误token
        local.clear();
        //回到登录
        router.push("/login")

    }
});

//设置默认地址
axios.defaults.baseURL='http://127.0.0.1:5000/'



//封装get和post请求
export default {
    //封装get 请求
    get(url, params) {
        return new Promise((resolve, reject) => {
            axios.get(url, { params }).then(res =>
                resolve(res.data)
            ).catch(erro =>
                reject(erro)
            )
        })
    },
    //封装post 请求
    post(url,params){
        return new Promise((resolve, reject) => {
            axios.post(url, qs.stringify(params)).then(res =>
                resolve(res.data)
            ).catch(erro =>
                reject(erro)
            )
        })
    }
}