/*
    登录和账户的接口api文件
*/
//引入请求文件
import request from "@/units/request.js"

//****************登录接口
//登录接口
export function api_login(params){
   return request.post("/users/checkLogin",params)
} 
// ****************添加账户接口
//添加账户接口
export function api_accAdd(params){
    return request.post("/users/add",params)
}
// ************************用户列表接口
//用户列表接口
export function api_accList(params){
    return request.get("/users/list",params)
}
//单项删除接口
export function api_accDel(params){
    return request.get("/users/del",params)
}
//批量删除接口
export function api_accAllDel(params){
    return request.get("/users/batchdel",params)
}
//编辑账户信息接口
export function api_accEidit(params){
    return request.post("/users/edit",params)
}
//********************修改密码api
//原密码api
export function api_eiditOldPwd(params){
    return request.get("/users/checkoldpwd",params)
}
//修改新密码api
export function api_accNewPwd(params){
    return request.post("/users/editpwd",params)
}
//获取用户信息api
export function api_getInfo(){
    return request.get("/users/info")
}
//头像修改上传api
export function api_updataImg(params){
    return request.get("/users/avataredit",params)
}
// 获取用户角色的 接口
export function api_getRole(){
    return request.get("/users/role")
}