<template>
  <div>
    <div class="rightTop">
      <!-- 面包屑 -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item v-for="item in barr" :key="item.title" :to="{ path: item.path }">
            {{ item.title }}
        </el-breadcrumb-item>
      </el-breadcrumb>
      <div class="avatar">
        <el-dropdown trigger="click" @command="selDown">
          <span>欢迎你,{{ account }}</span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="personal">个人中心</el-dropdown-item>
            <el-dropdown-item command="exit">退出登录</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
        <!-- 头像 -->
        <el-avatar :src="imgUrl" icon="el-icon-user-solid"></el-avatar>
      </div>
    </div>
  </div>
</template>

<script>
import { api_getInfo } from "@/api/acc";
import moment from "moment";
import local from "@/units/local";
export default {
  data() {
    return {
      imgUrl: "",
      account: "",
      barr: []    // 面包屑的数组
    };
  },
  methods: {
    //获取信息函数
    async getUserInfo() {
      let { accountInfo } = await api_getInfo();
      accountInfo.ctime = moment(accountInfo.ctime).format("YYYY-MM-DD HH:mm:ss");
      this.imgUrl = accountInfo.imgUrl;
      this.account = accountInfo.account;
    },
    //下拉选择函数
    selDown(val) {
      if (val == "personal") {
        this.$router.push("/account/personal");
      } else if (val == "exit") {
        local.clear();
        this.$router.push("/login");
      }
    },
    //面包削函数
    filterBread() {
      var bread = this.$route.matched;
      //   给一个数组添加上首页对象
      var barr = [{ title: "首页", path: "/" }];
      bread.forEach((item) => {
        // 判断如果title没有中文，那就不push
        if (item.meta.title != undefined) {
          barr.push({
            title: item.meta.title,
            path: item.path,
          });
        }
      });
      // 把计算出来的面包屑保存到定义的状态中，便于循环操作
      this.barr = barr;
    },
  },
  created() {
    this.getUserInfo();
    //更改图片头像即时变化
    this.$bus.$on("update", () => {
      // 重新调用一次获取详情接口
      this.getUserInfo();
    });
    this.filterBread();
  },
  watch: {
    // 监听hash值的变化
    "$route.path"() {
      // 监听到hash变化 调用计算面包屑的函数
      this.filterBread();
    },
  },
};
</script>

<style lang="less" scoped>
.rightTop {
  height: 50px;
  background: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 5px;
  margin-top: 5px;
  .avatar {
    display: flex;
    align-items: center;
    .el-avatar {
      margin: 0 20px;
    }
  }
}
</style>