<template>
  <div class="layout">
    <div class="leftMune">
      <h3 class="iconfont">外卖商家中心</h3>
      <el-menu
        background-color="#3a4b5d"
        text-color="#fff"
        unique-opened
        router
        :default-active="this.$route.path"
      >
        
        <template v-for="(item, i) in newMenus">
          <!-- 只有一个子路由 -->
          <el-menu-item
            v-if="item.children.length == 1"
            :key="i"
            :index="item.path"
          >
            <i class="iconfont icon-menu-home-normal"></i>
            <span slot="title">{{ item.meta.title}}</span>
          </el-menu-item>
          <!-- 有多个子路由的 -->
        <el-submenu v-if="item.children.length > 1" :key="i" :index="item.path">
          <template slot="title">
            <i class="iconfont icon-dan"></i>
            <span>{{item.meta.title}}</span>
          </template>
          <el-menu-item v-for="(v,i) in item.children" :key="i" :index="item.path+'/'+v.path">
            {{ v.meta.title }}
          </el-menu-item>
        </el-submenu>
        </template>
      </el-menu>
    </div>
    <div class="right">
      <RightTop></RightTop>
      <div class="rightBottom">
        <!-- 路由出口 -->
        <router-view />
      </div>
    </div>
  </div>
</template>

<script>
import { api_getRole } from "@/api/acc";
import RightTop from "@/views/layout/RightTop";
// 引入路由对象
import router, { asyncRoutes } from "@/router";
export default {
  components: {
    RightTop,
  },
  data() {
    return {
      newMenus: [],
    };
  },
  async created() {
    // 获取用户身份角色
    let { role } = await api_getRole();
    console.log(role);
    // 定义一个函数，用来判断当前路由对象 是否 有身份权限
    function hasRouter(role, routes) {
      // 判断 是否有meta并且有roles 如果有就是有权限，
      //如果没有就是不需要任何身份都可以访问的路由
      if (routes.meta && routes.meta.roles) {
        return routes.meta.roles.includes(role);
      } else {
        return true;
      }
    }

    // 定义一个函数，返回过滤出来的路由对象
    function filterRoutes(role, routes) {
      let newRoute = routes.filter((item) => {
        // 判断传入的路由对象是否有权限
        if (hasRouter(role, item)) {
          if (item.children) {
            // 递归过滤 二级路由
            item.children = filterRoutes(role, item.children);
          }
          return true;
        } else {
          return false;
        }
      });
      return newRoute;
    }

    let accRoutes = filterRoutes(role, asyncRoutes);
    // console.log(accRoutes);

    // 动态添加路由 router中 允许我们动态添加路由(添加过滤出来的路由对象)
    router.addRoutes(accRoutes);

    // 渲染左侧导航的数组对象
    this.menus = accRoutes;
    this.newMenus = this.menus.filter((item) => item.isshow);
    this.newMenus.forEach(item =>{
      if(item.children.length >1){
        console.log(item);
      }
    })
  },
};
</script>

<style lang="less" scoped>
.layout {
  display: flex;
  width: 100%;
  height: 100%;
  .leftMune {
    background: #3a4b5d;
    min-width: 200px;
    height: 100%;
    .el-menu {
      border: none;
    }
    .iconfont {
      margin-right: 5px;
      color: #fff;
    }
  }
  .right {
    display: flex;
    flex-direction: column;
    background: #f0f2f5;
    flex: 1;
    width: 100%;

    .rightBottom {
      padding: 20px;
    }
  }
}
</style>