import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)


//路由配置
import Login from '@/views/login'
import Layout from '@/views/layout'
//引入本地存储方法
import local from "@/units/local.js"
export const asyncRoutes = [
  { path: '/login', component: Login, isshow: false, },
  //后台首页
  {
    path: '/', component: Layout,
    redirect: "/home",
    isshow: true,
    meta: {
      title: "后台首页"
    },
    children: [{
      meta: {
        title: "后台首页"
      },
      path: "/home", component: () => import("@/views/layout/home/Home.vue"),
    }]
  },
  //账户管理
  {
    meta: {
      title: "账户管理",
      roles: ['super', 'normal']
    },
    path: '/account', component: Layout, isshow: true,
    
    children: [
      {
        meta: {
          title: "账户添加",
          roles: ['super']
        },
        path: "account-add",

        component: () => import("@/views/layout/account/AccAdd.vue")
      },
      {
        meta: {
          title: "密码修改"
        }, 
        path: "account-eidit",component: () => import("@/views/layout/account/AccEidit.vue")
      },
      {
        meta: {
          title: "账户列表",
          roles: ['super']
        },
        path: "account-list",
        component: () => import("@/views/layout/account/AccList.vue")
      },
      {
        meta: {
          title: "个人中心"
        },
        path: "personal",
        component: () => import("@/views/layout/account/Personal.vue")
      }
    ]
  },
  //商品管理
  {
    meta: {
      title: "商品管理"
    },
    path: '/goods', component: Layout, isshow: true,
    children: [
      {
        meta: {
          title: "商品添加"
        },
        path: "goods-add",
        component: () => import("@/views/layout/goods/GoodsAdd.vue")
      },
      {meta: {
        title: "商品分类"
      } ,path: "goods-type", component: () => import("@/views/layout/goods/GoodsType.vue") },
      {
        meta: {
          title: "商品列表"
        },
        path: "goods-list",
        component: () => import("@/views/layout/goods/GoodsList.vue")
      }
    ]
  },
  //订单管理
  {
    path: "/order", component: Layout, isshow: true, meta: {
      title: "订单管理"
    },
    children: [
      { path: "", component: () => import("@/views/layout/order/Order.vue") },
    ]
  },
  //销售统计
  {
    meta: {
      title: "统计管理",
      roles: ['super']
    },
    path: '/sale', component: Layout, isshow: true,
    redirect: "/sale/goods-acc",
    children: [
      {
        meta: {
          title: "商品统计"
        },
        path: "goods-acc", component: () => import("@/views/layout/sale/GoodsAcc.vue")
      }, {
        meta: {
          title: "订单统计"
        },
        path: "order-acc",
        component: () => import("@/views/layout/sale/OrderAcc.vue")
      },
    ]
  },
  //店铺管理
  {
    meta: {
      title: "店铺管理"
    },
    path: '/shop', component: Layout, isshow: true,
    children: [
      { path: "", component: () => import("@/views/layout/shop/Shop.vue") },
    ]
  },
  // 错误页面
  {
    path: "/404",
    isshow: false,
    component: () => import("@/views/Err404.vue")
  },
  // 容错页面
  {
    path: "*",
    isshow: false,
    redirect: "/404"
  }

]

const router = new VueRouter({
  routes: asyncRoutes
})

// 路由守卫
router.beforeEach((to, from, next) => {
  //取出本地token
  let istk = local.get("token") ? true : false
  //当路由地址为登录页面就放行，不做卫士
  if (to.path == "/login" || to.path == "/Login") {
    next()
  } else {
    if (istk) {
      next();
    } else {
      next("/login")
    }
  }
});

// 阻止重复点击报错
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}

export default router
