<template >
  <div id="login">
    <h2 class="title">系统登陆</h2>

    <el-form
      :model="loginForm"
      :rules="rules"
      ref="loginForm"
      label-width="100px"
      class="login-form"
    >
      <!-- 登录 -->
      <el-form-item label="账号" prop="account">
        <el-input
          prefix-icon="iconfont icon-zhanghao"
          v-model="loginForm.account"
          autocomplete="off"
        ></el-input>
      </el-form-item>
      <!-- 密码 -->
      <el-form-item label="密码" prop="password">
        <el-input
          prefix-icon="iconfont icon-mima"
          :suffix-icon="
            isclose ? 'iconfont icon-yanjing1' : 'iconfont icon-yanjing'
          "
          :type="isclose ? 'password' : 'text'"
          v-model="loginForm.password"
          autocomplete="off"
          @click.native="changeEye"
        ></el-input>
      </el-form-item>
      <!-- 登录 -->
      <el-form-item>
        <el-button type="primary" @click="loginBtn('loginForm')"
          >登录</el-button
        >
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
//引进api
import { api_login } from "@/api/acc.js";
//引进本地存储方法o
import local from "@/units/local"

export default {
  data() {
    return {
      isclose: true,
      //整个表单的双向绑定的数据
      loginForm: {
        account: "",
        password: "",
      },
      rules: {
        account: [
          { required: true, message: "请输入用户名", trigger: "blur" },
          { min: 3, max: 6, message: "请输入3-6位字符", trigger: "blur" },
        ],
        password: [
          { required: true, message: "请输入密码", trigger: "blur" },
          { min: 1, max: 6, message: "请输入6-12位字符", trigger: "blur" },
        ],
      },
    };
  },
  methods: {
    //登录按钮
    loginBtn() {
      this.$refs.loginForm.validate(async (valid) => {
        // 验证规则
        if (valid) {
          let { code, msg ,token} = await api_login(this.loginForm);
          if (code == 0) {
            //把token存入本地
            local.set("token",token)
            
            this.$router.push("/");
          } 
        } else {
          return false;
        }
      });
    },
    changeEye(e) {
      if (e.target.className.includes("icon-yanjing")) {
        this.isclose = !this.isclose;
      }
    },
  },
};
</script>

<style lang="less" scoped>
#login {
  user-select: none;
  height: 100%;
  background-color: #3a4b5d;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  .title {
    color: #fff;
    margin: 15px auto;
  }
  .login-form {
    width: 400px;
    height: 300px;
    /deep/ .el-form-item {
      .el-form-item__label {
        color: #fff;
      }
    }
    .el-form-item__content {
      .el-button {
        width: 100%;
      }
    }
  }
}
</style>